* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #dcdcdc;
  background-color: #2e8b57;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.loader {
  width: 100px;
  height: 100px;
  position: relative;
  top: 50%;
  left: 50%;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #0f0;
}

.error {
  display: none;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  color: #fff;
  background-color: rgba(0, 0, 0, .325);
  border: 2px solid #00fa9a;
  padding: 8px;
  font-size: 24px;
}

.breed-select:focus {
  outline: 2px solid #7fff00;
}

.cat-info {
  align-items: normal;
  margin-top: 40px;
  display: flex;
}

.cat-img {
  width: 600px;
  height: auto;
  box-shadow: 1px 1px 18px #fff;
}

.cat-text {
  border: 2px solid #fff;
  margin-left: 30px;
  padding: 16px;
}

.hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

